<template>
    <div class="message">
        <spam-communication />
        <leave-message :bid="bid" 
                        :rand-num="randNum" 
                        :now-page="nowPage"
                        @change:parent="changeParent" 
                        @totalCount="totalCount"/>
        
        <turn-page  :total-page="totalPage" 
                    :cur-page="nowPage" 
                    @turn-page="turnPage"/>

        <add-comment :bid="bid" 
                     :parent="parent" 
                     :parent-name="parentName" 
                     @reload="reloadPage" />
    </div>
</template>


<script>
import spamCommunication from "@/components/message/spamCommunication.vue"
import leaveMessage from "@/components/about/leaveMessage.vue"
import turnPage from "@/components/index/turnPage.vue"
import addComment from "@/components/about/addComment"
import { randomStr } from '@/util/random.js'
export default {
    components:{
        spamCommunication,
        leaveMessage,
        turnPage,
        addComment
    },
    data(){
        return {
            bid:-2,
            parent:-1,
            parentName:'',
            randNum:'',
            totalPage:10,
            nowPage:1,
            pageSize:2
        }
    },
    methods:{
         changeParent(data){
            location.href = '#send';
            this.bid = data.blogId;
            this.parentName = data.parent;
        },
        reloadPage(){
            location.href = '';
            this.parentName = '';
            this.randNum = randomStr(20); 
        },
        turnPage(i){
            this.nowPage = i;
        },
        totalCount(val){
            this.totalPage = Math.ceil(val/this.pageSize);
        }
    }
}
</script>


